
import { ref, onMounted } from "vue";
import { OrderDetail } from "@/classes/orderDetail";
export default {
  name: "OrdersDetail",
  props: ["OrderItems"],
  setup(props: any) {
    const totalQuantity = ref(0);
    const total = ref(0);
    onMounted(() => {
      props.OrderItems.forEach((el: OrderDetail) => {
        totalQuantity.value += el.quantity;
        total.value += el.quantity * el.price;
      });
    });
    return {
      totalQuantity,
      total,
    };
  },
};
