
import { computed } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
export default {
  name: "Nav",
  // props: ["userInfo"],
  setup() {
    const router = useRouter();

    const store = useStore();
    const userInfo = computed(() => store.state.User.user);

    const logout = () => {
      localStorage.clear();
      router.push("/login");
    };

    return {
      logout,
      userInfo,
    };
  },
};
