
import { ref, onMounted } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";

export default {
  name: "ProductsCreate",
  setup() {
    const name = ref(null);
    const description = ref(null);
    const price = ref(null);
    const categories = ref([]);
    const cateId = ref(null);
    const router = useRouter();
    const images = ref([{ id: 0 }]);
    const data = new FormData();
    //  load role for screen
    onMounted(async () => {
      const response = await axios.get("categories");

      categories.value = response.data.data;
    });

    const submit = async () => {
      data.append("name", name.value);
      data.append("description", description.value);
      data.append("price", price.value);
      data.append("cate_id", cateId.value);

      const response = await axios.post("products", data);
      if (response.status == 200) {
        await router.push("/products");
      }
    };

    // add mulity image
    const addRowImg = (index: number) => {
      images.value.push({
        id: index,
      });
    };
    const removeRowImg = (index: number) => {
      images.value.splice(index, 1);
    };

    const changeImg = (files: FileList) => {
      data.append("images[]", files.item(0));
    };
    return {
      submit,
      name,
      description,
      price,
      images,
      cateId,
      addRowImg,
      removeRowImg,
      categories,
      changeImg,
    };
  },
};
